.mode-selector-cont{
    display:flex;
    margin-top: 20px;
    width: 170px;
    justify-content: flex-end;
}
.mode-text{
    margin: auto 15px auto 0;
}
.mode-selector{
    width: 35px;
    height: 20px;
    /* background:white; */
    border-radius: 50px;
    position: relative;
    align-items: center;
    display: flex;
    border: 2px solid #1f1f1f;
}
.mode-button{
    height:22px;
    width:22px;
    /* background:#16262E; */
    position:absolute;
    border-radius: 100%;
    border: 2px solid #1f1f1f;
    /* left: -2px; */
    z-index: 1;
    cursor: pointer;
    
}
.mode-button:hover{
    background: #14d1ee;
}