@import url(https://fonts.googleapis.com/css2?family=Lato:ital,wght@0,300;0,400;0,700;0,900;1,100;1,300;1,400;1,700&display=swap);
* {
  margin: 0;
  padding: 0;
  font-family: "Lato", sans-serif;
  box-sizing: border-box;
}
p {
  cursor: default;
}
/* media queries */



.header-main-cont {
    padding: 60px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: flex-end;
            align-items: flex-end;
}

.main-body-cont {
    padding: 0 5rem;
    display: -webkit-flex;
    display: flex;
}

@media only screen and (max-width: 600px) {
    /*Big smartphones [426px -> 600px]*/
    .header-main-cont {
        padding: 20px;
    }
    .on-off-text {
        font-size: 12px;
    }
    .on-off-cont>img {
        height: 16px;
    }
    .mode-selector-cont {
        margin-top: 10px;
        font-size: 12px;
    }
    .main-body-cont {
        padding: 0 2rem;
        display: -webkit-flex;
        display: flex;
    }
}

.on-off-cont{
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    width: 170px;
    -webkit-justify-content: flex-end;
            justify-content: flex-end;
}
.on-off-text{
    margin-right: 15px;
    text-align: right;
}

.sidebar-lists {
  width: 270px;
  /* height: 800px; */
  padding-right: 10px;
}

.title-add-cont {
  display: -webkit-flex;
  display: flex;
  width: 100%;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  position: relative;
}

.add-list-name-modal {
  position: absolute;
  padding: 10px;
  width: 100%;
  top: -80px;
  z-index: 100;
  border-radius: 5px;
}

.a-t-tog {
  border-left: 25px solid transparent;
  border-right: 0 solid transparent;
  border-top: 20px solid white;
}

.a-t-off {
  border-left: 25px solid transparent;
  border-right: 0 solid transparent;
  border-top: 20px solid #d5dadd;
}

.alnm-triangle {
  width: 0;
  height: 0;
  position: absolute;
  bottom: -15px;
  right: 10px;
}

.add-name-input {
  font-size: 16px;
  background: none;
  border: none;
  padding: 7px;
  width: 100%;
  outline: none;
}

.list-names-cont {
  width: 100%;
  height: 100%;
  padding-top: 30px;
}

.list-name {
  padding: 15px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  position: relative;
}

.list-name-outer-cont {
  position: relative;
}

.list-active-bar {
  position: absolute;
  right: 0;
  top: 0;
  height: 100%;
  width: 3px;
}

.list-name:hover {
  background: #d5dadd;
  border-radius: 5px 0 0 5px;
  color: black;
}
/* ---------- new desktop button styles ----- */
.dtl-list-item{
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  position: relative;
  /* background: #bbbbbb; */
  width: 100%;
  border-radius: 5px;
  margin-bottom: 10px;
  background: #313131;
  cursor: pointer;
}
.dt-li-selector{
  width: 100%;
  padding: 15px 10px 15px 15px;
  cursor: pointer;
}
.dt-li-selector>p{
  cursor: pointer;
}
.dt-li-delete-btn{
  padding: 12px;
  /* background: rgba(240, 240, 240, 0.151); */
  border-radius: 0 5px 5px 0;
  cursor: pointer;
  display: -webkit-flex;
  display: flex;
}
.dt-li-delete-btn:hover{
  background: #b4b4b4;
}
/* --------------------- mobile menu styles ---------------------- */

.sbl-mb-overlay-open {
  background: #1f1f1fe8;
  transition: 150ms ease-in;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 999;
}

.sbl-mb-overlay-closed {
  opacity: 0;
  transition: 150ms ease-out;
}

.sbl-mb-list-cont {
  padding-top: 25px;
  margin-bottom: 100px;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.sbl-mb-cont-open {
  position: absolute;
  bottom: 0;
  left: 0;
  z-index: 9999;
  width: 100%;
  max-height: 500px;
  /* height: 100%; */
  display: block;
  background: #2E4756;
  border-radius: 5px 5px 0 0;
  transition: all 250ms ease-in;
  padding: 15px;
}

.sbl-mb-cont-closed {
  max-height: 0;
  transition: max-height 250ms ease-out;
}

.sbl-mb-header {
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.sbl-mb-h-add {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  padding-bottom: 25px;
}

.sbl-input-add-cont {
  width: 100%;
}

.sbl-input {
  padding: 15px;
  border-radius: 5px 0 0 5px;
  border: none;
  width: 80%;
  outline: none;
}

.mobile-add-btn {
  padding: 15px 0;
  width: 20%;
  border-radius: 0 5px 5px 0;
  border: none;
  background: rgba(255, 255, 255, 0.151);
  font-weight: bold;
  color: white;
}

.sbl-list-item {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  position: relative;
  /* background: #bbbbbb; */
  width: 100%;
  border-radius: 5px;
  margin-bottom: 10px;
}

.sbl-active, .dtl-active {
  position: absolute;
  bottom: 0;
  left: 0;
  height: 100%;
  width: 4px;
  border-radius: 5px 0 0 5px;
}

.mb-li-selector {
  width: 100%;
  padding: 12px 15px;
}

.mb-li-delete-btn {
  padding: 12px;
  background: rgba(255, 255, 255, 0.151);
  border-radius: 0 5px 5px 0;
}
.mb-add-img-false{
  -webkit-transform: rotate(0deg);
          transform: rotate(0deg);
  transition: all 250ms ease-out;
}
.mb-add-img-true{
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
  transition: all 250ms ease-in;
}
.mb-add-list-img{
  width: 200px;
  position: absolute;
  right: 23px;
  top: 50px;
}
.mb-add-list-img-hide{
  width: 200px;
  position: absolute;
  right: 50px;
  top: 120px;
}
/* ------------- media queries -------------- */

@media only screen and (max-width: 999px) {
  /*Tablets [601px -> 1000px]*/
  .sidebar-lists {
    height: 450px;
    min-width: 150px;
  }
  .title-add-cont, .list-names-cont {
    min-width: 150px;
  }
}

@media only screen and (max-width: 600px) {
  /*Big smartphones [426px -> 600px]*/
  .title-add-cont, .list-names-cont {
    width: 190px;
  }
}

@media only screen and (max-width: 1000px) {
  /*Big smartphones [426px -> 600px]*/
  .sidebar-lists {
    display: none;
  }
}
.progress-note{
    position: absolute;
    bottom: 0;
    left: 15px;
    font-size: 9px;
    font-weight: 700;
    /* opacity: 50%; */
}
.mode-selector-cont{
    display:-webkit-flex;
    display:flex;
    margin-top: 20px;
    width: 170px;
    -webkit-justify-content: flex-end;
            justify-content: flex-end;
}
.mode-text{
    margin: auto 15px auto 0;
}
.mode-selector{
    width: 35px;
    height: 20px;
    /* background:white; */
    border-radius: 50px;
    position: relative;
    -webkit-align-items: center;
            align-items: center;
    display: -webkit-flex;
    display: flex;
    border: 2px solid #1f1f1f;
}
.mode-button{
    height:22px;
    width:22px;
    /* background:#16262E; */
    position:absolute;
    border-radius: 100%;
    border: 2px solid #1f1f1f;
    /* left: -2px; */
    z-index: 1;
    cursor: pointer;
    
}
.mode-button:hover{
    background: #14d1ee;
}
.task-section {
  padding-left: 20px;
  /* background-color: rgb(0, 255, 85); */
  max-width: 600px;
  width: 100%;
}
.no-items-info-cont {
  margin-top: 25px;
}
.no-items {
  font-size: 35px;
}
.ni-mobile {
  display: none;
}
@media only screen and (max-width: 400px) {
  .ni-desktop {
    display: none;
  }
  .ni-mobile {
    display: block;
  }
}

.all-tasks-cont {
  margin-top: 10px;
  height: 100%;
  max-height: 470px;
  /* will need media query for smaller screens */
  overflow-x: hidden;
  overflow-y: scroll;
  padding-right: 10px;
  padding-bottom: 15px;
}
.all-tasks-cont::-webkit-scrollbar {
  width: 5px; /* width of the entire scrollbar */
}
.all-tasks-cont::-webkit-scrollbar-track {
  background: #00000000; /*color of the tracking area */
}
.all-tasks-cont::-webkit-scrollbar-thumb {
  background-color: #2e4756; /*color of the scroll thumb*/
  border-radius: 20px; /* roundness of the scroll thumb */
}

.task-cont {
  width: 100%;
  display: -webkit-flex;
  display: flex;
  /* background-color: aqua; */
  padding: 15px 20px;
  border-radius: 5px;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  /* background-color:#495a64 ; */
  margin-top: 10px;
}
.task-cont:hover {
  background: #445e6e;
}
.custom-checkbox {
  min-height: 20px;
  min-width: 20px;
  margin-right: 15px;
  /* cursor: pointer; */
  outline: none;
  border: none;
  border-radius: 5px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  color: #ffffff80;
}

/* task input box */
.task-input-cont {
  width: 100%;
}
.task-input {
  width: 100%;
  padding: 15px;
  border-radius: 5px;
  outline: none;
  border: none;
}
.task-span {
  text-transform: capitalize;
}
@media only screen and (max-width: 600px) {
  /* task section styles */
  .task-section {
    padding-left: 0px;
  }
 
}
@media only screen and (max-width:1440px) {
  .all-tasks-cont{
    max-height: 370px;
  }
}
@media (max-width:376px) and (max-height:813px){
	/* iphone x */
  .all-tasks-cont{
    max-height: 400px;
  }
}

@media (max-width:376px) and (max-height:668px){
	/* iphone 8 */
  .all-tasks-cont{
    max-height: 330px;
  }
}
@media (max-width:321px) and (max-height:569px){
	/* iphone se */
  .all-tasks-cont{
    max-height: 230px;
  }
}
.progress-bar-cont{
    /* position:absolute;
    left:0;
    top: 0; */
    margin-top: 10px;
    width: 565px;
    height:6px;
    mix-blend-mode:normal;
    background-color:#d5dadd;
    border-radius: 10px;
}
.progress{
    background-color: #00a0b8;
    height: 100%;
    border-radius: 10px;
}

@media only screen and (max-width: 600px){
	/*Big smartphones [426px -> 600px]*/
    .progress-bar-cont{
        width: 100%;
    }
}

.menu-icon {
    display: none;
    height: 60px;
    width: 60px;
    position: absolute;
    z-index: 99999;
    border-radius: 100%;
    bottom: 30px;
    left: 20px;
    background: #2E4756;
    /* box-shadow: 0 6px 13px rgba(0, 0, 0, 0.301); */
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}
.icon-cont-open{
    height: 30px;
    width: 30px;
    -webkit-align-items: center;
            align-items: center;
    display: -webkit-flex;
    display: flex;
    align-items: center;
}
.icon-cont{
    margin: auto;
    height: 20px;
    width: 30px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: space-between;
            justify-content: space-between;
}
.m-icon-item {
    background: white;
    height: 3px;
    border-radius: 100px;
}

.m-ii-one {
    width: 25px;
    transition: 200ms ease-in-out;
}

.m-ii-two {
    width: 30px;
    transition: 200ms ease-out;
}

.m-ii-three {
    width: 20px;
    transition: 200ms ease-out;
}
.menu-items-cont{
    max-height: 900px;
    transition: max-height 200ms ease-in;
}

.menu-closed{
    max-height:0;
    transition: max-height 340ms ease-out;
}

/* when menu is open */
.m-icon-o-one{
    position: absolute;
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
    width: 30px;
    transition: 200ms ease-in;
}
.m-icon-o-two{
    opacity: 0;
    width: 0px;
    transition: 100ms ease-out;
}
.m-icon-o-three{
    position: absolute;
    -webkit-transform: rotate(-45deg);
            transform: rotate(-45deg);
    width: 30px;
    transition: 200ms ease-in;
}
@media only screen and (max-width: 1000px){
	.menu-icon{
        display: -webkit-flex;
        display: flex;
    }
}
